.nav {
    position: fixed;
    top: 0;
    width: 100%;
    margin-bottom: 15px;
    color: var(--nav-text-color);
    background: var(--nav-background, transparent);
    z-index: 100;
    min-height: var(--nav-height, 50px);
    display: flex;

    &__icon {
        position: absolute;
        right: $global-padding;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: 40px;
        width: 40px;
        padding: 6px;
        border: solid 2px white;
        @include radius(3px);
        cursor: pointer;
        @include media-up($nav-breakpoint) {
            display: none; } }

    &__wrapper {
        @extend .wrapper;
        justify-content: space-between;
        align-items: center;

        &--fluid {
            @extend .wrapper--fluid;
            justify-content: space-between;
            align-items: center; } }

    &__links {
        display: none;
        padding: 0;
        margin: 0;
        list-style: none;
        @include media-up($nav-breakpoint) {
            display: flex; } }

    &__item {
        a {
            background-color: var(--nav-link-bg-color);
            @include radius;
            display: inline-flex;
            padding: 10px 18px;
            justify-content: center;
            align-items: center;
            text-align: center;
            text-decoration: none;
            color: var(--nav-link-text-color);

            &:hover,
            &.active {
                background-color: var(--nav-link-bg-color-active);
                color: var(--nav-link-text-color-active); } }

        &:not(:last-child) {
            margin-right: 10px; } }


    &__brand {
        font-size: var(--nav-brand-font-size, 20px);
        font-family: $global-header-font;
        a {
            text-decoration: none;
            color: var(--nav-brand-text-color, #fff); } } }


